html {
    height: 100%;
    overflow-y: scroll;
}

button.square {
    color: white;
    font-size: 70pt;
    border-radius: 20px;
    border-width: thick;
    background-color: black;
}

.square {
    color: #fff;
    -webkit-animation: square 1s ease-in-out infinite alternate;
    -moz-animation: square 1s ease-in-out infinite alternate;
    animation: square 1s ease-in-out infinite alternate;
    border: 1px solid #999;
    float: left;
    font-size: 28pt;
    font-weight: bold;
    line-height: 34px;
    height: 34px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 170px;
    height: 170px;
}

@keyframes square {
    from {
        text-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 30px #1336a8, 0 0 40px #1336a8, 0 0 50px #1336a8, 0 0 60px #1336a8, 0 0 70px #1336a8;
    }
    to {
        text-shadow: 0 0 20px #fff, 0 0 30px #4d94ff, 0 0 40px #4d94ff, 0 0 50px #4d94ff, 0 0 60px #121f97, 0 0 70px #121f97, 0 0 80px #121f97;
    }
}

.filler {
    min-height: 75vh;
    max-width: 1080px;
}

.bg {
    height: 100vh;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    overflow-y: scroll;
}

.board-row:after {
    clear: both;
    content: "";
    display: table;
}

.status {
    margin-bottom: 10px;
}

.square:focus {
    outline: none;
}

.kbd-navigation .square:focus {
    background: #ddd;
}

body {
    margin: 0;
    background-color: black;
}

.star {
    position: absolute;
    width: 1px;
    height: 1px;
    background-color: white;
}

/* Set the animation, color, size and hide the text */

.intro {
    top: 30%;
    z-index: 1;
    animation: intro 2s ease-out 1s;
    color: rgb(75, 213, 238);
    font-size: 18pt;
    font-weight: lighter;
    opacity: 0;
    min-height: 50vh;
    margin: 0;
    padding: 0;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
}

@keyframes intro {
    0% {
        opacity: 0;
    }
    20% {
        opacity: 1;
    }
    90% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}

/* Set the animation & hide the logo */

.logo {
    position: absolute;
    top: 20%;
    left: 45%;
    z-index: 1;
    margin: auto;
    animation: logo 30s ease-out;
    opacity: 0;
}

.logo svg {
    width: inherit;
}

/* Scale the logo down and maintain it centered */

@keyframes logo {
    0% {
        width: 18em;
        transform: scale(2.75);
        opacity: 1;
    }
    50% {
        opacity: 1;
        width: 18em;
    }
    100% {
        opacity: 0;
        transform: scale(0.1);
        width: 18em;
    }
}

p {
    color: #FFFF82;
}

/* Set the font, lean the board, position it */

#board {
    font-family: " Century Gothic, CenturyGothic, AppleGothic, sans-serif";
    transform: perspective(300px) rotateX(25deg);
    transform-origin: 50% 100%;
    text-align: justify;
    position: absolute;
    font-weight: bold;
    overflow: hidden;
    font-size: 350%;
    height: 50em;
    width: 100%;
    max-width: 1080px;
    bottom: 0;
    left: 0;
    /* background-color: green; */
    right: 0;
    /* top: 0; */
    margin-left: auto;
    margin-right: auto;
}

#board:after {
    position: absolute;
    content: ' ';
    bottom: 20%;
    left: 0;
    right: 0;
    top: 0;
}

/* Set the scrolling animation and position it */

#content {
    animation: scroll 130s linear 3s;
    position: absolute;
    top: 100%;
}

#title, #subtitle {
    text-align: center;
}

@keyframes scroll {
    0% {
        top: 100%;
    }
    100% {
        top: -170%;
    }
}

.starwarsImg {
